@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body{background:radial-gradient(circle, #8a2e00 0%, #0c56e6 100%);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;font-family:"Black Ops One", cursive}.loader{width:5rem;height:5rem;border:0.6rem solid #999;border-bottom-color:transparent;border-radius:50%;margin:0 auto;-webkit-animation:loader 500ms linear infinite;animation:loader 500ms linear infinite}@-webkit-keyframes loader{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loader{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loadingContainer{display:inline-block;position:absolute;left:45%;top:45%}.countries{background-color:green;border:2px black solid;margin-bottom:1rem;display:inline-block;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:95vw}.flag{width:35vw;position:relative;left:30%}button{border:none;border-radius:5%;background-color:#802f2f;color:white;height:1.5rem}input{height:1.5rem;width:20vw;border:none}input:focus{border:1px solid gray}h2{-webkit-text-decoration:black underline;text-decoration:black underline}h4{border:1px black dashed;margin:1rem;font-size:larger}

